import { Badge, Breadcrumb, Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import FeatherIcon from 'feather-icons-react';

//types
// import { PostItem } from './types';
import { Post } from 'components/blog';

// images
import img1 from 'assets/images/avatars/img-4.jpg';

type HeroContentProps = {
    content: Post,
    type: string
};

const pageUrl = `https://mydocbiz.com` + window.location.pathname;

const Hero = ({ content, type }: HeroContentProps) => {
    return (
        <section className="hero-4 pb-5 pt-8 pt-lg-6 pb-sm-4">
            <Container>
                <Row className="justify-content-center">
                    <Col lg={12}>
                        <Breadcrumb>
                            <Breadcrumb.Item href="/home">Home</Breadcrumb.Item>
                            <Breadcrumb.Item href={`/`+type+`s`}>Articles</Breadcrumb.Item>
                            <Breadcrumb.Item active>{ content.title }</Breadcrumb.Item>
                                    
                        </Breadcrumb>

                        <div className="mt-4">
                            <Link to="#">
                                <Badge bg="" className="badge-soft-primary mb-1">
                                    { type = type.charAt(0).toUpperCase() + type.slice(1) }
                                </Badge>
                            </Link>
                        </div>
                        <h1 className="hero-title mt-0">{ content.title }</h1>
                    </Col>
                </Row>

                <Row className="mt-4 align-items-center">
                    <Col xs="auto">
                        <div className="d-flex align-items-center">
                            <img className="me-2 avatar avatar-sm rounded-circle avatar-border" src={img1} alt="" />

                            <div>
                                <h5 className="m-0">
                                    <Link to="#"> { content.postedBy?.name} </Link>
                                </h5>
                                <p className="text-muted mb-0 fs-13">
                                    { content.postedOn?.date} · { content.postedOn?.time }
                                </p>
                            </div>
                        </div>
                    </Col>
                    <Col>
                        <div className="text-md-end">
                            <ul className="list-inline mb-0">
                                <li className="list-inline-item text-muted align-middle me-2 text-uppercase fs-13 fw-medium">
                                    Share:
                                </li>
                                <li className="list-inline-item me-2 align-middle">
                                    <a href={`https://www.facebook.com/sharer.php?u=`+pageUrl} target="_blank" rel="noopener">
                                        <FeatherIcon className="icon-xs icon-dual-primary" icon="facebook" />
                                    </a>
                                </li>
                                <li className="list-inline-item me-2 align-middle">
                                    <a href={`https://twitter.com/intent/tweet?text=Check%20out%20this%20article%20on%20MyDocBiz%20`+pageUrl}>
                                        <FeatherIcon className="icon-xs icon-dual-info" icon="twitter" />
                                    </a>
                                </li>
                                <li className="list-inline-item align-middle">
                                    <Link to="#">
                                        <FeatherIcon className="icon-xs icon-dual-danger" icon="instagram" />
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    );
};

export default Hero;
