import facebook from "assets/images/icons/facebook-circle.png";
import twitter from "assets/images/icons/twitter-circle.png";
import linkedin from "assets/images/icons/linkedin-circle.png";
import instagram from "assets/images/icons/instagram-circle.png";

type BackToTopProps = {
    variant: string;
};

const FloatRight = ({ variant }: BackToTopProps) => {
    return (
        <div className="sticky-container">
            <ul className="sticky">
                <li>
                    <img src={facebook} width="32" height="32" alt="facebook-icon"/>
                    <p><a href="https://www.facebook.com/mydocbiz" target="_blank" rel="noreferrer">Like Us on<br/>Facebook</a></p>
                </li>
                <li>
                    <img src={twitter} width="32" height="32" alt="twitter-icon"/>
                    <p><a href="https://twitter.com/MyDocBiz1" target="_blank" rel="noreferrer">Follow Us on<br/>Twitter</a></p>
                </li>
                <li>
                    <img src={linkedin} width="32" height="32" alt="linkedin-icon"/>
                    <p><a href="https://www.linkedin.com/company/mydocbiz" target="_blank" rel="noreferrer">Follow Us on<br/>LinkedIn</a></p>
                </li>
                <li>
                    <img src={instagram} width="32" height="32" alt="instagram-icon"/>
                    <p><a href="https://www.instagram.com/mydoc.biz/" target="_blank" rel="noreferrer">Follow us on<br/>Instagram</a></p>
                </li>
                
            </ul>
        </div>
    );
};

FloatRight.defaultProps = {
    variant: 'primary',
};

export default FloatRight;
