// components
import { Navbar1 } from 'components/navbars';
import { Footer1 } from 'components/footer';
// import { useParams } from 'react-router-dom';
import BackToTop from 'components/BackToTop';
import FloatRight from 'components/FloatRight';

import Hero from './Hero';
import PostContent from './PostContent';
// import PostNavigation from './PostNavigation';
// import Comments from './Comments';

// dummy data
import { gallery, investmentposts, blogposts } from './data';
// types
import { PostItem } from './types';

//get url params
let initial_url = window.location.pathname;
let location_path = initial_url.toString();
let locations = location_path.split( '/' );
let post_id = locations[3];
let post_type = locations[2];
let onepost: PostItem[];
if(post_type === 'investment'){
    onepost = investmentposts.filter((post) => {
        return post.id === post_id;
    });  
    // console.log('invpost');
}
if(post_type === 'blog'){
    onepost = blogposts.filter((post) => { 
        return post.id === post_id;
    });
    //console.log('blogpost');
}

const BlogPost = () => {
    return (
        <>
            <div>
                <Navbar1
                    hideSearch
                    fixedWidth
                    navClass="navbar-light zindex-10 shadow"
                    buttonClass="btn-outline-secondary btn-sm"
                />
                {onepost.map((item, index) => {
                    return (
                        <div key={index.toString()}>
                        <Hero content={item} type={post_type} />
                        </div>
                    );
                })}
                
            </div>

            {/* post content */}
            {onepost.map((item, index) => {
                return (
                    <div key={index.toString()}>
                        <PostContent gallery={gallery} content={item} postid={post_id}/>
                    </div>
                );
            })}
            

            {/* navigation */}
            {/* <PostNavigation /> */}

            {/* comments */}
            {/* <Comments /> */}

            {/* footer */}
            <Footer1 />

            <BackToTop />
            <FloatRight />
        </>
    );
};

export default BlogPost;
